/* Navigation Bar */
.topnav {
  background-color: #333;
  overflow: hidden;
  border-radius: 1.565vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 3.13vh;
  height: 11.738vh;
}

/* Navigation Bar Links */
.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  text-decoration: none;
  font-size: 3.13vh;
  padding: 11.738vh 0;
}
.topnav a:not(:first-child) {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  padding-left: 2.504vh;
  padding-right: 2.504vh;
}

/* Links on hover */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}

/* Logo */
.topnav a.logo {
  background-color: #aa0404;
  color: white;
  padding: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

/* Stage Fright Logo */
.logoimage {
  width: 7.825vh;
  height: 7.825vh;
  padding: 1.565vh;
  margin: 0.313;
}

/* Tour Grid Container */
.tourdiv {
  display: grid;
  gap: 0; /* No extra spacing between rows or columns */
  background-color: #dbdbdb;
  border: 0.158vh solid black; /* Outer border */
  margin-bottom: 3.13vh; /* Added spacing between tourdivs */
  box-sizing: border-box; /* Ensure padding and border are included in size */
}

/* Remove bottom margin for the last .tourdiv */
.tourdiv:last-child {
  margin-bottom: 0;
}

/* Each Tour Row */
.tourrow {
  display: grid;
  grid-template-columns: 1fr auto auto;
  border-bottom: 0.158vh solid black; /* Consistent bottom border */
}

/* Remove bottom border for last row */
.tourrow:last-child {
  border-bottom: 0;
}

/* Tour Info Section */
.tourinfo {
  display: flex;
  flex-direction: column; /* Stack items as a column */
  align-items: flex-start; /* Align content to the left */
  justify-content: center; /* Center content vertically */
  padding: 1.565vh 1.565vh 0 1.565vh; /* Updated padding */
  height: 100%; /* Ensure it takes full height of the row */
  box-sizing: border-box; /* Include padding in height */
}

/* Buy Tickets Section */
.buytickets {
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  padding: 1.565vh; /* Updated padding */
}

.buytickets button {
  background-color: black;
  border: 0.156vh solid black;
  color: white;
  padding: 0.784vh 4.695vh;
}

/* Typography for Tour Details */
.date {
  font-size: 1.878vh;
  margin-bottom: 0.784vh; /* Consistent spacing */
}
.city {
  font-size: 2.817vh;
  margin-bottom: 0.784vh;
}
.location {
  font-size: 2.191vh;
  padding-bottom: 0.784vh; /* Updated padding */
}
.tourname {
  font-size: 4.695vh;
  text-align: center;
  text-shadow: 0.313vh 0.313vh 0.784vh red;
  font-family: wolfhagen-blackletter;
  margin-top: 0;
}
@font-face {
  font-family: wolfhagen-blackletter;
  src: url("../../public/Fonts/wolfhagen_blackletter/WolfhagenBlackletter-Regular.ttf");
}

body {
  margin: 1.252vh;
}
/* Importing OpenDyslexic font */
@font-face {
  font-family: "OpenDyslexic";
  src: url("../../public/Fonts/opendyslexic/OpenDyslexic-Regular.woff2") format("woff2"),
      url("../../public/Fonts/opendyslexic/OpenDyslexic-Regular.woff") format("woff"); /* Fallback */
  font-weight: normal;
  font-style: normal;
}

/* High Contrast Mode */
.high-contrast {
  --background-color: #000000;
  --text-color: #ffffff;
  --link-color: #00ffff;
  --button-background: #ffcc00;
  --button-text: #000000;
  --border-color: #ffffff;

  background-color: var(--background-color) !important;
  color: var(--text-color) !important;
  border-color: var(--border-color) !important;
}

.high-contrast a {
  color: var(--link-color) !important;
  text-decoration: underline;
}

.high-contrast button,
.high-contrast input[type="button"],
.high-contrast input[type="submit"] {
  background-color: var(--button-background) !important;
  color: var(--button-text) !important;
}

/* Ensure text in tourinfo adopts high-contrast colors */
.high-contrast .tourinfo,
.high-contrast .tourinfo span {
  color: #000000 !important;
}

.high-contrast .buytickets button {
  background-color: var(--button-background) !important;
  color: var(--button-text) !important;
  border: 0.1vh solid var(--border-color) !important;
}

/* Open Dyslexic Font Mode */
.open-dyslexic {
  font-family: "OpenDyslexic", sans-serif !important;
}

/* Example for buttons and form inputs */
.open-dyslexic input,
.open-dyslexic textarea,
.open-dyslexic button {
  font-family: "OpenDyslexic", sans-serif !important;
}

/* Specifically target elements using Wolfhagen Blackletter and override */
.open-dyslexic .tourname {
  font-family: "OpenDyslexic", sans-serif !important;
  text-shadow: none; /* Optionally remove the text-shadow for readability */
}